.social-icons {
  padding: 0.25rem 0;
  font-size: 3rem; }
  @media (max-width: 425px) {
    .social-icons {
      font-size: 2.5rem; } }
  .social-icons a {
    padding: 0 0.625rem; }
  .social-icons.animate-icons a {
    display: inline-block;
    -webkit-transform: perspective(1px) translateZ(0);
            transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent; }
    .social-icons.animate-icons a:hover, .social-icons.animate-icons a:focus, .social-icons.animate-icons a:active {
      -webkit-animation-name: icon-jump;
              animation-name: icon-jump;
      -webkit-animation-duration: 0.45s;
              animation-duration: 0.45s;
      -webkit-animation-timing-function: ease-in-out;
              animation-timing-function: ease-in-out;
      -webkit-animation-iteration-count: 1;
              animation-iteration-count: 1; }

@-webkit-keyframes icon-jump {
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes icon-jump {
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

.scroll-to-next {
  cursor: pointer; }
