@import '../../styles/breakpoints';
.about-page {
  padding-top: 2rem;
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center;

  h1 {
    font-family: 'Julius Sans One', sans-serif;
    padding: 1rem 0;
    font-size: 3rem;

    @include breakpoint-small-down {
      font-size: 2rem;
    }
  }

  .content-grid {
    flex: auto;
  }

  .about-wrapper {
    .about-content {
      font-size: 0.875rem;
      line-height: 1.5rem;

      p {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: 400;
        padding: 0.5rem 0;
      }

      .text-emoji {
        padding: 2rem 0;
        font-size: 1.5rem;
        font-weight: 300;
        text-align: center;
      }
    }
  }
}
